import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex);

export const store = new Vuex.Store({
  state: {
    _coins: {},
    _coinData: {}
  },
  getters: {
    getCoins: function(state) {
      return state._coins
    },
    getCoinData: function(state) {
      return state._coinData
    }
  },
  mutations: {
    setCoins: function(state, data) {
      state._coins = data;
    },
    setCoinData: function(state, data) {
      state._coinData = data;
    }
  },
  actions: {
    getCoinData: function (context) {
      axios.get("https://www.cryptocompare.com/api/data/coinlist")
        .then(function(response){
          context.commit('setCoinData', response.data.Data);
          axios.get("https://api.coinmarketcap.com/v1/ticker/?limit=10")
            .then((resp) => {
              context.commit('setCoins', resp.data)
            })
            .catch((err) => {
              console.log(err);
            })
        })
        .catch(function(err) {
          console.log(err)
        })
    },
    getCoins: function(context) {
      axios.get("https://api.coinmarketcap.com/v1/ticker/?limit=10")
        .then((resp) => {
          context.commit('setCoins', resp.data)
        })
        .catch((err) => {
          console.log(err);
        })
    }
  }
});



